
export function configRouter (router) {
  // normal routes
  router.map({
    '/home': {
      component: require('./views/home.vue')
    },
    '/instruction': {
      component: require('./views/instruction.vue')
    },
    '/products': {
      component: require('./views/products.vue')
    },
    '/productDetail': {
      component: require('./views/productDetail.vue')
    },
    '/news': {
      component: require('./views/news.vue')
    },
    '/newsDetail': {
      component: require('./views/newsDetail.vue')
    },
    '/contactus': {
      component: require('./views/contactus.vue')
    }
  })
  // redirect
  router.redirect({
    '/index': '/home',
    '/': '/home'
  })
}



