<style scoped>
.autoplay{
  height:235px;
}
ul{
  position: relative;
}
li{
  position: absolute;
  width:100%;
}
img{
  width:100%;
  height:235px;
}
</style>

<template>
  <div class="autoplay">
    <ul>
      <li><img :src="autoImg1" alt="" class="autoplayImg"/></li>
      <li><img :src="autoImg2" alt="" class="autoplayImg"/></li>
      <li><img :src="autoImg3" alt="" class="autoplayImg"/></li>
      <li><img :src="autoImg4" alt="" class="autoplayImg"/></li>
    </ul>
  </div>
</template>

<script>
import autoImg1 from '../assets/images/img/1.jpg'
import autoImg2 from '../assets/images/img/2.png'
import autoImg3 from '../assets/images/img/3.jpg'
import autoImg4 from '../assets/images/img/4.jpg'

export default {
  data () {
    return {
      autoImg1,
      autoImg2,
      autoImg3,
      autoImg4
    }
  },
  ready () {
    this.autoplayFun()
  },
  methods: {
    autoplayFun: function () {
      let imgs = document.getElementsByClassName('autoplayImg')
      let index = 0
      let len = imgs.length
      console.log(imgs)
      setInterval(function () {
        for (var i = 0; i < len; i++) {
          imgs[i].style.display = 'none'
        }
        imgs[index].style.display = ''
        index++
        if (index === len) {
          index = 0
        }
      }, 3000)
    }
  }
}
</script>



