export function getProductList (state) {
  if (parseInt(state.productParentId) === 0) {
    return state.productList
  } else {
    let list = []
    for (var i = 0, len = state.productList.length; i < len; i++) {
      if (state.productList[i].parentId === parseInt(state.productParentId)) {
        list.push(state.productList[i])
      }
    }
    return list
  }
}
export function getAllProductList (state) {
  return state.productList
}

export function getProductParentList (state) {
  return state.productParentList
}

export function getProductId (state) {
  return state.productId
}

export function getProductParentId (state) {
  return state.productParentId
}



