<style scoped>
.detail{
  padding: 3px 0;
  font-size: 12px;
}
.detail>div{
  padding: 5px;
  border: 1px solid #ddd;
}
.first{
  overflow: hidden;
}
.first>span{
  float: right;
  margin-right: 25px;
  cursor:pointer;
}
.second{
  text-align: center;
}
.second img{
  width:660px;
  height:440px;
}
.second div{
  margin: 10px 0;
}
.third{
  text-align: center;
}
</style>
<template>
  <div class="detail">
    <div class="first">
      <span @click='previous'>上一个:{{productList[nowIndex-1]?productList[nowIndex-1].imgTitle:''}}</span>
      <span @click='last'>下一个:{{productList[nowIndex+1]?productList[nowIndex+1].imgTitle:''}}</span>
    </div>
    <div class="second" v-for="product in productList" v-show="$index === nowIndex">
      <img :src="product.imgUrl" alt="" />
      <div class="">
        产品类别:<span>{{product.parentName}}</span>
      </div>
      <div class="">
        产品名称:<span>{{product.imgTitle}}</span>
      </div>
    </div>
    <div class="third">
      <span>Copyright &copy 诸暨市驰敏气动元件有限公司 All Rights Reserved.</span>
    </div>
  </div>
</template>
<script>
// import { getProductList } from '../vuex/getters'
import store from '../vuex/store'
export default {
  // vuex: {
  //   getters: {
  //     productList: getProductList
  //   }
  // },
  data () {
    return {
      store,
      nowIndex: this.$route.query.id - 1,
      productList: store.state.productList
    }
  },
  methods: {
    previous: function () {
      if (this.nowIndex === 0) {
        return
      } else {
        this.nowIndex--
      }
    },
    last: function () {
      if (this.nowIndex === this.productList.length) {
        return
      } else {
        this.nowIndex++
      }
    }
  }
}
</script>



