<style scoped>
.title{
  width:245px;
  padding:5px 10px;
  margin: 0 auto;
  font-size: 13px;
  border-bottom: 1px dotted #999;
}
.product .title{
  padding-left: 20px;
}
.product div{
  cursor: pointer;
}
.search select,
.search input{
  display: block;
  width:70%;
  margin-bottom:5px;
  margin-left: 10%;
}
.title:hover{
  color:red;
  cursor: pointer;
  text-decoration: underline;
}
</style>

<template>
  <aside class="">
    <div class="news">
      <img :src="news" alt="" />
      <div class="title" v-link="{path:'/newsDetail'}">
        <img :src="point" alt="" />
        <span>热烈庆祝诸暨市驰敏气动元件有限公司网站成立[2016-09-23]</span>
      </div>
      <div class="title" v-link="{path:'/newsDetail'}">
        <img :src="point" alt="" />
        <span>热烈庆祝诸暨市驰敏气动元件有限公司网站成立[2016-09-23]</span>
      </div>
    </div>
    <div class="product">
      <img :src="product" alt="" />
      <div v-for='product in productParentList' class="title"  @click="getProductParentId(product.id)" v-link="{path:'/products'}" id="{{product.id}}">
        <img :src="point" alt="" />
        &nbsp;
        <span >{{product.name}}</span>
      </div>
    </div>
    <div class="search">
      <img :src="search" alt="" />
      <select class="" name="" v-model='parentId'>
        <option selected>请选择类别</option>
        <option v-for='productParent in productParentList' :value="productParent.id" >{{productParent.name}}</option>
      </select>
      <select class="" name="" v-model='productId'>
        <option v-show="parentId == ''"></option>
        <option
          v-for="allProduct in allProductList"
          v-show="allProduct.parentId == parentId"
          :value='allProduct.productId'
          >{{allProduct.imgTitle}}</option>
      </select>
      <input type='button' name="name" value="开始搜索" @click="toProductDetail()">
    </div>
  </aside>
</template>

<script>
import news from '../assets/images/img/news.gif'
import product from '../assets/images/img/product.gif'
import search from '../assets/images/img/search.gif'
import point from '../assets/images/img/point.gif'
import { getProductParentId } from '../vuex/actions'
import { getProductParentList, getAllProductList } from '../vuex/getters'
export default {
  data () {
    return {
      news,
      product,
      search,
      point,
      parentId: '',
      productId: ''
    }
  },
  vuex: {
    actions: {
      getProductParentId: getProductParentId
    },
    getters: {
      productParentList: getProductParentList,
      allProductList: getAllProductList
    }
  },
  methods: {
    toProductDetail: function () {
      this.$route.router.go({
        path: '/productDetail',
        query: {
          id: this.productId
        }
      })
    }
  }
}
</script>



