<style scoped>
ul{
  height:456px;
  padding: 0 25px;
}
li{
  display: inline-block;
  width:213px;
  height:149px;
  text-align: center;
  margin-bottom: 3px;
}
li img{
  width:162px;
  height:127px;
}
li span{
  display: block;
  font-size: 14px;
}
.pageControll{
  text-align: center;
  font-size: 14px;
}
.fun{
  cursor:pointer;
}
strong{
  color:#FF0000;
}
</style>
<template>
  <div class="">
    <img :src="productDisplay" alt="" />
    <ul>
      <li v-for='product in productList' v-show="$index>=lowLimit && $index<=highLimit" v-link="{path:'/productDetail',query:{id:product.productId}}">
        <img :src="product.imgUrl" alt="" />
        <span>{{product.imgTitle}}</span>
      </li>
    </ul>
    <div class="pageControll">
      <span>共 <strong>{{productList.length}}</strong> 条记录</span>
      <span>每页 <strong>9</strong> 条记录</span>
      <span>当前第 <strong>{{page}}</strong> / {{Math.ceil(productList.length/9)}} 页</span>
      <span @click='fisrtPage' class="fun">首页</span>
      <span @click='previousPage' class="fun">上一页</span>
      <span @click='nextPage' class="fun">下一页</span>
      <span @click='lastPage' class="fun">末页</span>
    </div>
  </div>
</template>
<script>
import productDisplay from '../assets/images/img/products-l.gif'
import { getProductList, getProductParentId } from '../vuex/getters'
export default {
  data () {
    return {
      lowLimit: 0,
      highLimit: 8,
      page: 1,
      totalPage: 5,
      productDisplay
    }
  },
  vuex: {
    getters: {
      productParentId: getProductParentId,
      productList: getProductList
    }
  },
  methods: {
    fisrtPage: function () {
      this.lowLimit = 0
      this.highLimit = 8
      this.page = 1
    },
    previousPage: function () {
      if (this.lowLimit !== 0) {
        this.lowLimit -= 9
        this.highLimit -= 9
        this.page--
      }
    },
    nextPage: function () {
      if (this.highLimit !== Math.ceil(this.productList.length / 9) * 9 - 1) {
        this.lowLimit += 9
        this.highLimit += 9
        this.page++
      }
    },
    lastPage: function () {
      this.lowLimit = (Math.ceil(this.productList.length / 9) - 1) * 9
      this.highLimit = Math.ceil(this.productList.length / 9) * 9 - 1
      this.page = Math.ceil(this.productList.length / 9)
    }
  }
}
</script>



